@import url('https://fonts.googleapis.com/css?family=Bungee|Bungee+Outline');
@import url('/static/monosocialiconsfont/monosocialiconsfont.css');

:root {
  --primaryColor: #00cfc0;
  --linkTextColor: #000;
  --linkHoverColor: #fff;
  --pagePadding: 1.5em;
  --gradientTopPadding: 7em;
}

:global(body) {
  display: flex;
  align-items: flex-end;
  margin: 0;
  padding: 0;
  min-height: 100vh;
  background-color: #000;
  background-image: url('/static/wires-min.jpg');
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  font-family: 'Mono Social Icons Font';
}

:global(#__next) {
  width: 100%;
}

/* https://github.com/drinchev/monosocialiconsfont/issues/37 */
:global(a.symbol), :global(a.symbol:before) {
  -ms-font-feature-settings: 'liga' 1;
}

:global(.nowrap) {
  white-space: nowrap;
}

.content {
  background: linear-gradient(to bottom, rgba(0,0,0,0), rgba(0,0,0,0.8));
  padding: var(--pagePadding);
  padding-top: var(--gradientTopPadding);
  margin-top: calc(var(--pagePadding) - var(--gradientTopPadding));
}

.header {
  color: var(--primaryColor);
}

.title,
.subtitle,
.footerText {
  line-height: 1.2em;
}

.title {
  position: relative;
  font-family: 'Bungee Outline';
  font-weight: 400;
  font-size: 5em;
  margin: 0;
  /* Font quirkiness workarounds */
  margin-left: -1px;
  margin-top: -0.2em;
}
.title:before,
.title:after {
  font-family: 'Bungee';
  position: absolute;
  content: attr(data-title);
}

.title:before {
  z-index: -1;
  top: 0;
  left: 0;
  color: rgba(0,0,0,0.5);
}

.title:after {
  z-index: -2;
  top: 0.05em;
  left: 0.05em;
  color: rgba(0,0,0,0.0);
}

.subtitle {
  font-weight: 400;
  font-size: 1em;
  margin: 0.7em 0 5em;
}

.footerList {
  font-size: 1.4em;
  list-style-type: none;
  margin: 0;
  margin-bottom: -0.5em;
  padding: 0;
}

.footerItem {
  display: inline-block;
  margin-right: 0.5em;
  margin-bottom: 0.5em;
}

a.link {
  color: var(--linkTextColor);
  background-color: var(--primaryColor);
  border-radius: 5px;
}

a.link:before {
  line-height: 1em;
}

a.link:hover {
  background-color: var(--linkHoverColor);
}

@media only screen and (max-width : 375px) {
  .content {
    font-size: 0.8em;
  }
}